.faq__block-title{
   text-align: center;
}
.faq__footer{
   margin-top: 80px;
}
.sidebar{
   width: 344px;
   border: 1px solid #E6E9F2;
   border-radius: 3px;
}
.sidebar__item{
   cursor: pointer;
   border-top: 1px solid #E6E9F2;
}
.sidebar__title:hover{
   color: $color_blue;
}
.sidebar__title:hover.arrow::after{
   border-top: 3px solid $color_blue;
}
.sidebar__subtitle:hover{
   color: $color_blue;
}
.sidebar__subtitle:hover.arrow::after{
   border-top: 3px solid $color_blue;
}
.sidebar__item:first-child{
   border-top: none;
}
.sidebar__title{
   transition: all 0.3s ease 0s;
   position: relative;
   padding: 20px 30px 20px 20px;
}
.sidebar__title.arrow::after{
   top: 28px;
   right: 20px;
}
.sidebar__title.active.arrow::after{
   top: 25px;
   transform: rotate(-180deg);
}
.sidebar__title.active{
   font-weight: 600;
   background-color: $color_light;
}
.sidebar__title.active ~ .sidebar__menu{
   background-color: $color_light;
}
.sidebar__subtitle{
   position: relative;
   transition: all 0.3s ease 0s;
   border-top: 1px solid #E6E9F2;
   padding: 17px 0px 17px 0px;
   margin: 0 40px 0 25px
}
.sidebar__subtitle.arrow::after{
   top: 25px;
   right: -20px;
}
.sidebar__subtitle.active.arrow::after{
   top: 25px;
   transform: rotate(-180deg);
}
.sidebar__title.active.arrow::after{
   top: 25px;
   transform: rotate(-180deg);
}
.sidebar__subtitle.active{
   font-weight: 600;
}
.sidebar__menu{
   display: none;
}
.sidebar__submenu{
   display: none;
   padding-bottom: 5px;
}
.sidebar__sub-subtitle{
   padding: 0 25px 10px 35px;
   display: block;
   width: 100%;
}
.--active{
   font-weight: 600;
}
.faq__right{
   stroke: #97A3B0;
   transition: all 0.3s ease 0s;
}
.faq__block-text{
   max-width: 784px;
   width: 100%;
}
.faq__title{
   margin-top: 30px;
}
.faq__menu{
   margin-top: 20px;
   width: 100%;
}
.faq__item{
   padding: 20px 32px;
   border: 1px solid #E6E9F2;
   border-bottom: none;
}
.faq__item:hover{
   border-color: transparent;
   box-shadow: 0px 5px 20px rgba(64, 72, 98, 0.1);
}
.faq__item:hover > .faq__right{
   stroke: $color_blue;
}
.faq__item:last-child{
   border: 1px solid #E6E9F2;
}
.social--faq{
   margin-top: 50px;
}
.social__btn--faq{
   border: 1px solid #E6E9F2;
}
.social__btn--faq:hover{
   border: transparent;
}
.faq__list-link{
   width: 100%;
   margin-top: 50px;
}
.link-left{
   margin-right: 15px;
   stroke: $color_blue;
   transition: all 0.3s ease 0s;
}
.faq__item-link:hover > .link-left{
   stroke: #333;
}
.faq__item-link:hover > h5{
   color: #333;
}
.link-right{
   margin: 0;
   margin-left: 15px;
}