.footer {
   background: linear-gradient(0deg, #26263E, #26263E), linear-gradient(0deg, #30303B, #30303B), linear-gradient(0deg, #FFFFFF, #FFFFFF), #F8F8F8;
   color: #fff;
}
.footer__menu {
   display: flex;
   justify-content: space-between;
   padding: 50px 0;
   margin: 0 -10px;
}
.footer__item {
   display: flex;
   max-width: 220px;
   width: 100%;
   flex-direction: column;
   margin: 0 10px;
}
.footer__title {
   font-weight: 600;
   margin-bottom: 6px;
}
.footer__title:hover,
.footer__email:hover {
   color: #fff;
   opacity: 0.7;
}
.footer__subtitle:hover,
.footer__confidentiality:hover{
   color: #fff;
   opacity: 0.9;
}

.footer__subtitle {
   opacity: 0.75;
}
.footer__email {
}
.footer__copy {
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 30px 0;
   margin: 0 -20px;
}
.footer__line{
   opacity: 0.1;
   height: 1px;
   background-color: #fff;
}
.footer__copyright,
.footer__confidentiality {
   opacity: 0.5;
}
.social {
   column-gap: 7px;
   justify-content: flex-end;
   flex-grow: 1;
}
.social__btn {
   background: rgba(255, 255, 255, 0.2);
}
