.about-item__text{
   min-width: 0;
}
.about-slider__slider-text{
   position: relative;
   min-width: 0;
}
/* Слайдер */
.slick-slider{
}
/* Слайдер запущен */
.slick-slider.slick-initialized{}
/* Слайдер с точками */
.slick-slider.slick-dotted{}
/* Ограничивающая оболочка */
.slick-list {
   overflow: hidden;
}
/* Лента слайдов */
.slick-track{
   width: 1212px;
   display: flex;
}
/* Слайд */
.slick-slide{}
/* Слайд активный (показывается) */
.slick-slide.slick-active{}
/* Слайд основной */
.slick-slide.slick-current{}
/* Слайд по центру */
.slick-slide.slick-center{}
/* Клонированный слайд */
.slick-slide.slick-cloned{}
/* Стрелка */
.slick-arrow{
   content: '';
   position: absolute;
   font-size: 0;
   top: 45%;
   padding: 0;
   background-color: #F5F7FD;
}
/* Стрелка влево */
.slick-arrow.slick-prev{
   width: 42px;
   height: 42px;
   background-image: url("data:image/svg+xml;charset=utf8, %3Csvg width='7' height='12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 1L1 6l5 5' stroke='%23333' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
   background-repeat: no-repeat;
   background-position: center;
   left: -80px;
}
.slick-arrow.slick-prev:hover{
   background-color: $color_blue;
   opacity: 1;
   background-image: url("data:image/svg+xml;charset=utf8, %3Csvg width='7' height='12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 1L1 6l5 5' stroke='%23fff' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}
/* Стрелка вправо */
.slick-arrow.slick-next{
   width: 42px;
   height: 42px;
   background-image: url("data:image/svg+xml;charset=utf8, %3Csvg width='7' height='12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1l5 5-5 5' stroke='%23F5F7FD' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M1 1l5 5-5 5' stroke='%23333' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
   background-repeat: no-repeat;
   background-position: center;
   right: -80px;
   transition: all 0.3s ease 0s;
}
.slick-arrow.slick-next:hover{
   background-color: $color_blue;
   opacity: 1;
   transition: all 0.3s ease 0s;
   background-image: url("data:image/svg+xml;charset=utf8, %3Csvg width='7' height='12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1l5 5-5 5' stroke='%23F5F7FD' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M1 1l5 5-5 5' stroke='%23fff' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}
/* Стрелка не активная */
.slick-arrow.slick-disabled{}
/* Точки (булиты) */
.slick-dots{
   content: '';
   display: flex;
   position: absolute;
   font-size: 0;
   bottom: 50px;
   left: 47%;
}
.slick-dots li{
   margin: 0 5px;
}
/* Активная точка */
.slick-dots li.slick-active{
}
/* Элемент точки */
.slick-dots li button{
   background-color: #DAE0EC;
   width: 10px;
   height: 10px;
   padding: 0;
}
.slick-dots li.slick-active button{
   background-color: #5E84FF;
}