.header{
   display: flex;
   align-items: center;
   justify-content: space-between;
   background-color: $color_light;
   padding: 30px 0px 30px 0;
}
.burger{
   display: none;
}
.logo{
   display: flex;
   align-items: center;
}
.logo__cloud{
   margin-right: 8px;
}
.nav-menu{
   display: flex;
   flex-grow: 1;
   justify-content: center;
}
.nav-menu__item,
.nav-menu__title{
   position: relative;
}
.nav-menu__item{
   padding: 0 20px;
}
.authorization > button{
   margin: 0 4px;
}
.menu{
   right: -115px;
   top: 30px;
}
.menu__item{
   position: relative;
   padding: 15px 18px;
   border-bottom: 1px solid #EDF0F7;
   transition: all 0.3s ease 0s;
}
.menu__title{
   display: block;
   width: 100%;
   color: #000;
}
.menu__title.arrow::after{
   top: 23px;
   right: 18px;
   transform: rotate(-90deg);
}
.submenu,
.sub-submenu{
   top: 0;
   right: -250px;
}
.nav-menu__item:hover .menu,
.menu__item:hover .submenu,
.menu__item:hover > .sub-submenu{
   opacity: 1;
   visibility: visible;
}
.menu__item:hover{
   background-color: $color_light;
   transition: all 0.3s ease 0s;
}