.platform {
   background-color: $color_light;
}
.platform__text {
   display: flex;
   flex-direction: column;
   max-width: 549px;
   flex-grow: 1;
}
.platform__paragraph {
   margin-top: 20px;
}
.form--platform {
   max-width: 498px;
   margin-top: 20px;
}
.form__input--platform {
   width: 297px;
   padding-right: 5px;
}
.platform__details-list {
   margin-top: 50px;
   display: grid;
   grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
   gap: 28px 32px;
}
.platform__details-item {
   display: flex;
   padding: 25px 30px 18px 32px;
   background: #FFF;
   border: 1px solid #E1E5F0;
   border-radius: 4px;
   justify-content: space-between;
   transition: all 0.3s ease 0s;
}
.platform__details-item:hover{
   border-color: transparent;
   box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.1);
}
.platform__description {
   max-width: 79.2%;
}
.platform__description-text {
   margin-top: 8px;
}