body{
   font-family: 'Montserrat', sans-serif;
   font-weight: 400;
   color: #333;
}

:hover{
   transition: all 0.3s ease 0s;
}
::placeholder{
   font-size: 16px;
   line-height: 28px;
   color: #97A3B0;
}
input{
   padding: 10px 15px;
}
.container{
   @include container('1212px', '10px')
}
.wrapper--about-item{
   padding: 100px 10px;
}
.wrapper{
   padding: 50px 10px;
}
a{
   color: inherit;
   transition: all 0.3s ease 0s;
}
a:hover{
   color: $color_hover;
}
a:hover.arrow::after{
   border-top: 3px solid $color_hover;
}
.anim-hover{
   transition: all 0.3s ease 0s;
}
button{
