.arrow::after{
   content: '';
   border: 3px solid transparent;
   border-top: 3px solid #333;
   position: absolute;
   right: -10px;
   top: 7px;
   transition: all 0.3s ease 0s;
}
.drop-list{
   position: absolute;
   z-index: 1;
   background-color: #fff;
   box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.15);
   border-radius: 4px;
   width: 250px;
   opacity: 0;
   visibility: hidden;
   transition: opacity 0.2s, visibility 0.2s;
   transition-delay: 0.15s;
}
.form {
   position: relative;
}
.form::after{
   content: '';
   opacity: 0;
   position: absolute;
   top: -4px;
   right: -5px;
   width: 101.5%;
   height: 116.5%;
   z-index: 0;
   border: 4px solid #e0e7fb;
   border-radius: 4px;
   transition: all 0.3s ease 0s;
}
.form:hover.form::after{
   opacity: 1;
}
.form__input {
   position: relative;
   z-index: 1;
   height: 54px;
   border-radius: 4px 0 0 4px;
   border: 1px solid #d8e0e6;
   transition: all 0.3s ease 0s;
}
.form__input:focus{
   border: 1px solid #839FF6;
}
.form__btn {
   position: absolute;
   top: 0;
   z-index: 1;
   border-radius: 0 4px 4px 0;
   padding: 17px 30px;
   margin-left: -3px;
}
.social {
   display: flex;
   column-gap: 7px;
}
.social__btn {
   width: 39px;
   height: 39px;
   padding: 0;
   display: flex;
   align-items: center;
   justify-content: center;
}
.social__btn > svg{
   fill: #a8a8b2;
   transition: all 0.3s ease-in-out;
}
.social__btn:hover > svg{
   fill: #fff;
   transition: all 0.3s ease-in-out;
}
